import React from "react";

// Custom Hook para Almacenar datos en LocalStorage
function useLocalStorage(itemName, initialValue) {
  const [error, setError] = React.useState(false);
  const [loading, setLoading] = React.useState(true);
  const [item, setItem] = React.useState(initialValue);

  React.useEffect(() => {
    try {
      setTimeout(() => {
        const localStorageItem = localStorage.getItem(itemName); // Obtengo valor del LocalStorage
        let parsedItem; // Donde almaceno el valor inicial parseado (stringifiado ðŸ˜…)

        // Corroboro que exista
        if (!localStorageItem) {
          localStorage.setItem(itemName, JSON.stringify(initialValue)); // Convierto en String al Item y lo almaceno
          parsedItem = initialValue; // Almaceno initialValue parseado
        } else {
          parsedItem = JSON.parse(localStorageItem); // Parseo el valor del LocalStorage a JSON
        }

        setItem(parsedItem);
        setLoading(false);
      }, 1000);
    } catch (error) {
      setError(error);
    }
  }, [itemName, initialValue]);

  const saveItem = (newItem) => {
    try {
      const stringifiedItem = JSON.stringify(newItem);
      localStorage.setItem(itemName, stringifiedItem);
      setItem(newItem);
    } catch (error) {
      setError(error);
    }
  };

  return { item, saveItem, loading, error };
}

export default useLocalStorage;
